.GrossParent {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 50px 600px 200px 200px 200px 200px 200px;
    grid-template-areas: "N" "R" "W" "S" "F" "C" "A";
}

.Navigation {
    grid-area: N;
    background-color: fuchsia;
    color: yellow;
    text-align: center;
    grid-column: 1/13;
}

.hidden-menu {
    display: block;
    position: fixed;
    list-style:none;
    margin: 0;
    box-sizing: border-box;
    width: 100%;
    background: rgba(0, 170, 238, 0.9); /* Цвет фона */
    color: #fff; /* Цвет текста */
    padding: 150px; /* Поля вокруг текста */
    font-weight: bold;
    font-size: 50px;
    text-decoration: none;
    height: 100%;
    top: 0;
    right: -100%;
    transition: right .2s;
    z-index: 2;
    transform: translateZ(0);
    backface-visibility: hidden;
}
.hidden-menu a {
    text-decoration: none;
}

.hidden-menu a:hover {
    color: yellow;
    background-color: orange;
}

.hidden-menu-ticker {
    display: none;
}

.btn-menu {
    color: #fff;
    background-color: red;
    padding: 5px;
    position: fixed;
    top: 5px;
    right: 5px;
    cursor: pointer;
    transition: right .23s;
    z-index: 3;
    width: 25px;
    transform: translateZ(0);
    backface-visibility: hidden;
}
.btn-menu span {
    display: block;
    height: 5px;
    background-color: yellow;
    margin: 5px 0 0;
    transition: all .1s linear .23s;
    position: relative;
}
.btn-menu span.first {
    margin-top: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu {
    right: 95%;
}
.hidden-menu-ticker:checked ~ .hidden-menu {
    right: 0;
}
.hidden-menu-ticker:checked ~ .btn-menu span.first {
    transform: rotate(45deg);
    top: 10px;
}
.hidden-menu-ticker:checked ~ .btn-menu span.second {
    opacity: 0;
}
.hidden-menu-ticker:checked ~ .btn-menu span.third {
    transform: rotate(-45deg);
    top: -10px;
}

.Resume {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "rO rT";
    grid-area: R;
    background-color: yellow;
    color: green;
    text-align: center;
    grid-column: 1/13;
}

.resumeOne {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: "rO1" "rO2" "rO3" "rO4";
    grid-area: rO;
    grid-column: 1/7;
    background-color: lightgreen;
}

.rO1 {
    grid-area: rO1;
    width: 100%;
    background-color: red;
    color: yellow;
    align-self: end;
}

.rO1 span {
    font-size: 50px;
    font-weight: bold;
    font-family: Ubuntu;
}

.rO2 {
    grid-area: rO2;
    width: 100%;
    background-color: yellow;
    color: red;
    align-self: end;
}

.rO2 span {
    font-size: 50px;
    font-weight: bold;
}

.rO3 {
    grid-area: rO3;
    width: 100%;
    background-color: orange;
    color: yellow;
}

.rO3 span {
    background-color: blue;
    font-size: 70px;
    font-weight: bold;
    color: yellow;
}

.rO4 {
    grid-area: rO4;
    width: 100%;
    background-color: green;
    color: yellow;
}

.rO4 span {
    background-color: blue;
    font-size: 50px;
    font-weight: bold;
}

.resumeTwo {
    grid-area: rT;
    grid-column: 7/13;
    background-color: cyan;
    position: relative;
}

.rT1 {
    width: 50%;
    height: 70%;
    position:absolute;
    right:15px;
    top: 15px;
    background-color: yellow;
    transition: 10s;
}

.Resume:hover .rT1 {
    transform: translate(-300px,150px);
    transition: all 10s ease-in-out;
    /*-webkit-transform: translate(350px,-350px);*/
    /*-o-transform: translate(350px,-350px);*/
    /*-moz-transform: translate(350px,-350px);*/
}

.rT2 {
    width: 50%;
    height: 70%;
    position:absolute;
    right:15px;
    top: 15px;
    background-color: blue;
}

.MyWorks {
    grid-area: W;
    color: cyan;
    text-align: center;
    grid-column: 1/13;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


.MyServices {
    grid-area: S;
    background-color: red;
    color: yellow;
    text-align: center;
    grid-column: 1/13;
}

.Freelanc {
    grid-area: F;
    background-color: lightgreen;
    color: blue;
    text-align: center;
    grid-column: 1/13;
}

.Contacts {
    grid-area: C;
    background-color: lavender;
    color: red;
    text-align: center;
    grid-column: 1/13;
}

.Footer {
    grid-area: A;
    background-color: gold;
    color: fuchsia;
    text-align: center;
    grid-column: 1/13;
}













